.type {
  line-height: 1;
  padding: 5px 0;
  margin: 1px 0;
  border: 1px solid #aaa;
  border-radius: 2px;
  color: #fff;
  font-size: 0.8rem;
  text-transform: uppercase;
  text-shadow: 1px 1px 1px #333;
  width: 58px;
  display: inline-block;
  text-decoration: none;
}
.type:link:hover,
.type:link:active,
.type:link:focus {
  opacity: 0.85;
}
.type-bug {
  background: #a8b820;
  background-image: -webkit-linear-gradient(#a8b820, #97a61d);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#a8b820), to(#97a61d));
  background-image: -o-linear-gradient(#a8b820, #97a61d);
  background-image: linear-gradient(#a8b820, #97a61d);
  border-color: #86931a;
}
