div.Filter {
  display: -ms-flexbox;
  display: flex;
}
.FilterType select {
  font-size: 1rem;
}
option.type-,
option.type-unknown,
option.type-shadow,
select.type-,
select.type-unknown,
select.type-shadow {
  background: #fff;
  color: #000;
}
select.type {
  padding: 0 0;
  width: 7rem;
  text-shadow: 0 0 0;
}
.BtnClear {
  background-color: inherit;
  border: 0;
}
.BtnClear:hover {
  background-color: #e9ecef;
}



