.pagination {
  display: -ms-flexbox;
  display: flex;
  padding-left: 0;
  list-style: none;
  -webkit-margin-before: 0.5rem;
  -webkit-margin-after: 0.5rem;
/*border-radius: .25rem*/
}
.page-item {
  display: block;
  list-style: none;
}
/*  padding: 0 4px
  margin: 0 2px*/
.page-link {
  display: block;
  text-decoration: none;
  color: #007bff;
  padding: 0.5rem 0.5rem;
  min-width: 1.25rem;
  text-align: center;
}
.page-item {
  cursor: pointer;
  display: block;
  margin-left: -1px;
  line-height: 1.25;
  color: #007bff;
  background-color: #fff;
  border: 1px solid #dee2e6;
}
.page-item:hover {
  color: #0056b3;
  background-color: #e9ecef;
  border-color: #dee2e6;
}
.page-item:first-child {
  border-radius: 4px 0 0 4px;
  margin-left: 0;
}
.page-item:last-child {
  border-radius: 0 4px 4px 0;
}
/*  border: 2px solid #007bff  */
.page-item.disabled {
  z-index: 1;
  color: #404040;
  cursor: default;
}
.page-item.disabled .page-link {
  color: #404040;
}
.page-item.active {
  color: #fff;
  background-color: #007bff;
  border-color: #007bff;
}
.page-item.active .page-link {
  color: #fff;
}



