.avatar {
  min-width: 75px;
  max-width: 75px;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
  border-color: #808080;
  text-align: center;
}
/*tbody tr:nth-child(odd) 
  background-color: #fff

*/
tbody tr:nth-child(even) {
  background-color: #f4f4f2;
}
/*tbody tr 
  max-height: 54px 
  min-height: 54px

*/
th,
td {
  border-width: 1px 0;
  border-style: solid;
  border-color: #c4c4c4 #d6d6d6;
  display: table-cell;
  padding: 3px 10px;
  vertical-align: middle;
}
th {
  padding: 6px 10px;
  background-color: #e7e7e0;
  background-image: -webkit-linear-gradient(#e7e7e0, #dbdbd2);
  background-image: -webkit-gradient(linear, left top, left bottom, from(#e7e7e0), to(#dbdbd2));
  background-image: -o-linear-gradient(#e7e7e0, #dbdbd2);
  background-image: linear-gradient(#e7e7e0, #dbdbd2);
  border-width: 1px;
  text-align: left;
  white-space: nowrap;
}
td:first-child {
  border-left-width: 1px;
}
td:last-child {
  border-right-width: 1px;
}
th.avatar {
  text-align: left;
}
td.avatar {
  padding: 0 10px;
  vertical-align: middle;
}
td.id {
  text-align: right;
  line-height: 1.2;
}
th.name,
td.name {
  text-align: left;
  min-width: 8rem;
  max-width: 8rem;
}
td.name {
/*font-size: 16px*/
  font-size: 1.2rem;
  font-weight: bold;
  line-height: 1.2;
}
th.number {
  min-width: 2.6em;
  max-width: 2.6em;
  padding: 6px 0;
  text-align: center;
}
td.number {
  text-align: right;
  min-width: 2.6em;
  max-width: 2.6em;
}
td.number-total {
  font-weight: bold;
}
td.col-types {
  line-height: 0.1;
}



